import CloneFormModel from "../domain/CloneFormModel.js";
import FormModel from "../domain/FormModel.js";

export default class FormService {
    /**
     *
     * @param {string} baseURL
     */
    constructor(baseURL) {
        this.baseURL = baseURL;
    }
    /**
     *
     * @returns {object}
     */
    async getForms() {
        try {
            const { data } = await axios(`${this.baseURL}/forms/properties`);

            return { success: true, forms: data.data };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     *
     * @param {FormModel} form
     * @returns {object}
     */
    async createNew(form) {
        try {
            const { data } = await axios.post(
                `${this.baseURL}/pagebuilder/form`,
                form
            );
            if (data.success) {
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     *
     * @param {FormModel} form
     * @returns {object}
     */
    async update(form) {
        try {
            const { data } = await axios.put(
                `${this.baseURL}/pagebuilder/form`,
                form
            );
            if (data.success) {
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     *
     * @param {string} formId
     * @returns
     */
    async deleteFormBy(formId) {
        try {
            const { data } = await axios.delete(
                `${this.baseURL}/pagebuilder/form?form_id=${formId}`
            );

            if (data.success) {
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     * @param {CloneFormModel} form
     */
    async cloneForm(form) {
        try {
            console.log(form);
            // const { data } = await axios.post(`${this.baseURL}`, form);
            if (true) {
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
}
