import CategoryModel from "../domain/CategoryModel.js";

export default class CategoryService {
    /**
     *
     * @param {string} baseURL
     */
    constructor(baseURL) {
        this.baseURL = baseURL;
        this.categories = [];
    }

    /**
     * @returns {object}
     */
    async getCategories() {
        try {
            if (this.categories.length > 0) {
                return { success: true, categories: this.categories };
            }
            const { data } = await axios(
                `${this.baseURL}/pagebuilder/categories`
            );
            if (data.success) {
                this.categories = data.data;
                return { success: true, categories: data.data };
            }
            return { success: false };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     *
     * @param {CategoryModel} category
     */
    async create(category) {
        try {
            const { data } = await axios.post(
                `${this.baseURL}/pagebuilder/category`,
                category
            );
            if (data.success) {
                this.categories = [];
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     *
     * @param {CategoryModel} category
     * @returns {object}
     */
    async update(category) {
        try {
            const { data } = await axios.put(
                `${this.baseURL}/pagebuilder/category`,
                category
            );
            if (data.success) {
                this.categories = [];
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
    /**
     *
     * @param {string} categoryId
     * @returns {object}
     */
    async deleteBy(categoryId) {
        try {
            const { data } = await axios.delete(
                `${this.baseURL}/pagebuilder/category?category_id=${categoryId}`
            );
            if (data.success) {
                this.categories = [];
                return { success: true };
            }
            return { success: false, message: data.message };
        } catch (error) {
            return { success: false };
        }
    }
}
